import './App.css';
import { useAuth0 } from '@auth0/auth0-react';
import axios from 'axios';
import { useEffect, useState } from 'react';
import JoditEditor from 'jodit-react'



function App() {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState(); 
  const { loginWithPopup, logout, user } = useAuth0();
  const [text, setText] = useState('');
  const [color1, setColor1] = useState('#633FA6');
  const [color2, setColor2] = useState('#FFFFFF');
  const [auth, setAuth] = useState('');
  const [check, setCheck] = useState(true);



  const config = {
    toolbarAdaptive: false, 
    buttons: 'bold,italic,underline,hr,|,superscript,subscript,|,ul,ol,align,|,indent,outdent,|,font,fontsize,|,table,|,undo,redo,|,selectall,cut,copy,paste,|,find,fullsize,print',
    style: {
      backgroundColor: color1,
      color: color2,
      minHeight: "500px",
    },
    spellcheck: true,
    
    placeholder: 'Enter text here...',
    readonly: false,
  };



  function menubar() {
    setOpen(!open);
  }



  function Login(){
    loginWithPopup();
  }
  function Logout(){
    logout();
    localStorage.removeItem('auth');
    setCheck(true);
  }
  async function handleLogin() {

    try {
      const result = await axios.post(
        `/auth/signup`,
        { email: user ? user.email : auth.email }
      )
      if (result.data.success) {
        setData(result.data.data);
        localStorage.setItem('auth', JSON.stringify({ name: user ? user.name : auth.name, email: user ? user.email : auth.email }));
        setCheck(false);
      }
      else {
        alert("Error from here", result.data.error);
      }
    }
    catch (err) {
      
      alert("Internal server error from frontend in useEffect");
    }
  }
  useEffect(() => {
    if (localStorage.getItem('auth')) {
      let auths = JSON.parse(localStorage.getItem('auth'));
      
      setAuth(auths);
    }
    if ((user || auth) && check) {
      handleLogin();
    }
  }, [user]); 

  
  
  function removeHtmlTags(input) {
    let htmlTagRegex = /<[^>]*>/g;
    let result = input.replace(htmlTagRegex, '');
    result = result.replace(/&nbsp;/g, '');
    return result;
  }
  function handleChange(content) {
    setText(content);
  };



  async function handleSave() {
    try {
      const result = await axios.post(
        `/notes/post`,
        { email: user ? user.email : auth.email, data: text }
      )
      if (result.data.success) {
        alert(result.data.message);
        handleLogin(); 
      }
      else {
        alert(result.data.error);
      }
    }
    catch (err) {
      alert("Internal server error via frontend");
    }
  }



  async function handleDelete(id) {
    let que = window.confirm("Are you sure want to delete this");
    if (que) {
      try {
        const result = await axios.post(
          `/notes/delete`,
          { email: user ? user.email : auth.email, id: id }
        )
        if (result.data.success) {
          handleLogin();
        }
        else {
          alert(result.data.error);
        }
      }
      catch (err) {
        alert("Internal server error via frontend");
      }
    }
  }



  async function handleFetchOne(id) {
    try {
      const result = await axios.post(
        `/notes/getone`,
        { id: id }
      )
      if (result.data.success) {
        alert(result.data.message);
        setText(result.data.notes.data);
      }
      else {
        alert(result.data.error);
      }
    }
    catch (err) {
      alert("Internal server error via frontend");
    }
  }



  function handleColor(e) {
    if (e.target.name === 'bg-color') {
      setColor1(e.target.value);
    }
    if (e.target.name === 'txt-color') {
      setColor2(e.target.value);
    }
  }



  return (
    <div>

      <button onClick={menubar} className="link-button-menu">
        History
      </button>



      <div className='navbar'>
        {
          user || auth ? (
            <div className='user-info'>
              {user ? user.name : auth.name}
            </div>
          ) : (
            <div className='user-info'>
              Signin is necessary
            </div>
          )
        }
        {
          user || auth ? (
            <button className='link-button-sign' onClick={Logout}>Logout</button>
          ) : (
            
            
            <button className='link-button-sign' onClick={Login}>Signin</button>
          )
        }
      </div>



      <div className='tool-bar'>
        Color- <input type='color' name='bg-color' value={color1} onChange={handleColor} className='tool-bar-input' />
        Text- <input type='color' name='txt-color' value={color2} onChange={handleColor} className='tool-bar-input' />
      </div>



      <div className={`side-menu-bar ${open ? 'show-menu' : ''}`}>
        <button onClick={menubar} className="link-button-menu-in">
          History
        </button>
        <br />
        {
          user || auth ? (
            <div>
              {
                data ? (
                  <div>
                    {
                      data.data.map((dt, i) => {
                        const dtaa = removeHtmlTags(dt.data);
                        return (
                          <div className="menu-content-wrapper" key={i}>
                            <span className="menu-content-left" onClick={() => { handleFetchOne(dt._id) }}>
                              {dtaa.substring(0, 25)}...
                            </span>
                            <span className="menu-content-right">
                              <button className='link-button-delete' onClick={() => { handleDelete(dt._id) }}>Delete</button>
                            </span>
                          </div>
                        )
                      })
                    }
                  </div>
                ) : (
                  <div className="menu-content-wrapper">
                    No data yet
                  </div>
                )
              }
            </div>
          ) : (
            <div className="menu-content-wrapper">
              Sign in to retrieve your data history
              <button className='link-button-sign' onClick={Login}>Signin</button>
            </div>
          )
        }
      </div>



      <div>
        {
          user || auth ? (
            <div className='input-content-div'>
              <div className='JustForSaveButton'>
                <button className='link-button-save' onClick={handleSave}>Save</button>
              </div>

              <JoditEditor
                config={config}
                value={text}
                onBlur={handleChange}
              />

            </div>

          ) : (
            <div className="display-content-div">
              Signin to write content
              <button className='link-button-sign' onClick={Login}>Signin</button>
            </div>
          )
        }
      </div>



    </div>
  );
}

export default App;